import React from "react";
import PropTypes from "prop-types";
import { AvForm, AvField, AvGroup } from "availity-reactstrap-validation";
import { Button, Label, Alert } from "reactstrap";
import { Card,CardBody,CardHeader,Row, Col } from 'reactstrap';

class AddMaterialTypesForm extends React.Component{
    state={
        data:{
            material:""
        },
        errors:{},
        loading:false,
        success:false
    }
    onChange = e => {
        this.setState({
          ...this.state,
          data: { ...this.state.data, [e.target.name]: e.target.value }
        });
    };

    handleValidSubmit = () => {
        console.log(this.state.data);
        this.setState({ loading: true });
        this.props
          .submit(this.state.data)
          .then(() => {
            this.setState({ loading: false,errors:{},success:true});
            this.myFormRef.reset();
          })
          .catch((err) =>{
            this.setState({ errors: err.response.data.errors, loading: false }) 
          });
    };

    render(){
        const { data, errors, loading, success } = this.state;
        return(
            <div>
                {!!success ? (
                    <Alert color="info">Material details is Added successfully</Alert>
                ) : null}
                {!!errors.materialname ? (
                    <Alert color="danger">Material data already exist.</Alert>
                ) : null}
                <AvForm
                    onValidSubmit={this.handleValidSubmit}
                    onInvalidSubmit={this.handleInvalidSubmit}
                    ref={el => (this.myFormRef = el)}
                >
                    <Card>
                        <CardHeader>Add Item Type</CardHeader>
                        <CardBody>
                        <Row>
                            <Col sm={6}>
                                <AvGroup>
                                    <Label className='small-font-size' for="material">Item</Label><span className="required-label">*</span>
                                    <AvField
                                        name="material"
                                        type="text"
                                        id="material"
                                        value={data.option}
                                        onChange={this.onChange}
                                        validate={{
                                            required: {
                                            value: true,
                                            errorMessage: "Please enter Item"
                                            },
                                   
                                            pattern: {
                                            value:
                                                "^[a-zA-Z]",
                                            errorMessage:
                                                "Please enter valid materialname"
                                            }
                                        }}
                                    />   
                                </AvGroup>
                            </Col>
                            <Col sm={6}>
                                <AvGroup className="mg-20">
                                    <Button color="primary" disabled={loading} className="lgn-loader">
                                        Submit
                                        {/* {loading} */}
                                    </Button>
                                </AvGroup>
                            </Col>
                        </Row>
                        </CardBody>
                    </Card>
                </AvForm>                        
            </div>
        );
    }
}

AddMaterialTypesForm.propTypes = {
    submit: PropTypes.func.isRequired
};

export default AddMaterialTypesForm;