import React from 'react';
import { connect } from 'react-redux';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';
import {
  Button,
  Label,
  Alert,
  Card,
  CardBody,
  CardHeader,
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter
} from 'reactstrap';
import PropTypes from 'prop-types';

class UpdateItemListForm extends React.Component {
  state = {
    data: {
      id: null,
      masteritemtype: '',
      masteritemname: '',
      itemName: '',
      masteritemdescription: '',
      masteritemshortdescription:'',
      masteritemuom: '',
      itemImage: '',
      costperunit: null,
      // masteritemrate: null,
      masteritemunitwt: null,
      masteritemimagefile: null,
      permeter: null,
      perfeet: null,
      perinch: null
    },
    materialoptionlistvalues: [],
    uom: [],
    errors: {},
    loading: false,
    success: false,
    imageChanged: false,
    filename:''
  };

  componentDidMount() {
    const id = this.props.id;
    const materialoptionlistvalues = Object.values(
      this.props.materialoptionlistvalues
    );
    const uomlistvalues = Object.values(this.props.uomlistvalues);
    this.setState({
      ...this.state,
      materialoptionlistvalues,
      uom: uomlistvalues
    });
  }

  componentWillReceiveProps(props) {
    const id = props.id;
    const masteritemlistvalues = props.masteritemlistvalues[id];
    console.log(masteritemlistvalues)
    const data = {};
    data.id = masteritemlistvalues.id;

    data.masteritemname = masteritemlistvalues.masteritemtypename;
    data.masteritemdescription = masteritemlistvalues.masteritemdescription;
    data.masteritemshortdescription = masteritemlistvalues.masteritemshortdescription;
    data.itemName = masteritemlistvalues.itemName;
    
    data.permeter = masteritemlistvalues.permeter;
    data.masteritemunitwt = masteritemlistvalues.masteritemunitwt;

    data.perfeet =  masteritemlistvalues.permeter / 3.2808.toFixed(2)
    data.perinch =  masteritemlistvalues.permeter / 39.3701.toFixed(2)
    data.itemImage = masteritemlistvalues.masteritemimage != undefined && 
                     masteritemlistvalues.masteritemimage != null &&
                     masteritemlistvalues.masteritemimage != '' ? `${process.env.REACT_APP_IMAGE_BASE_URL}` + '/' + masteritemlistvalues.masteritemimage : ''
    
    console.log(data.itemImage)
    data.masteritemtype = masteritemlistvalues.masteritemtype._id;
    data.masteritemuom = masteritemlistvalues.masteritemuom._id;
    data.costperunit = masteritemlistvalues.masteritemcostperunit != undefined && 
                       masteritemlistvalues.masteritemcostperunit != null ? 
                      masteritemlistvalues.masteritemcostperunit.toString() : 0
    const materialoptionlistvalues = Object.values(
      props.materialoptionlistvalues
    );
    const uomlistvalues = Object.values(props.uomlistvalues);
    this.setState({
      ...this.state,
      materialoptionlistvalues,
      uom: uomlistvalues,
      data
    });
  }

  onNameChange = e => {
    this.setState({
      ...this.state,
      data: { ...this.state.data, ['itemName']: e.target.value, ['masteritemshortdescription']: e.target.value }
    });
  }

  createSelectItems = () => {
    let items = [];
    const materialoptionlistvalues = this.state.materialoptionlistvalues;
    materialoptionlistvalues.map((option, i) => {
      items.push(
        <option key={i} value={option.id}>
          {option.masteritemtypename}
        </option>
      );
    });
    return items;
  };

  createSelectUomItems = () => {
    let items = [];
    const uomlistvalues = this.state.uom;
    uomlistvalues.map((option, i) => {
      items.push(
        <option key={i} value={option.id}>
          {option.uomname}
        </option>
      );
    });
    return items;
  };

  onChange = e => {
    this.setState({
      ...this.state,
      data: { ...this.state.data, [e.target.name]: e.target.value }
    });
  };

  onChange2 = e => {
    this.setState({
      ...this.state,
      data: { ...this.state.data, [e.target.name]: e.target.value,
        perfeet : (e.target.value / 3.2808).toFixed(2),
        perinch : (e.target.value / 39.3701).toFixed(2)
       },

    });
  };


  fileSelectedHandler = e => {
    console.log(e.target.files[0]);
    let masteritemimagefile = e.target.files[0];
    let file_size = e.target.files[0].size / 1024 / 1024;
    let file_name = e.target.files[0].name;
    let file_type = e.target.files[0].type;
    console.log(`${file_size} MB`);
    console.log(file_name);
    console.log(file_type);
    if (Number(file_size) > 2) {
      this.setState({ errors: { filesize: 'File Size is greater than 2MB' } });
    }
    if (!this.hasExtension(file_name, ['.jpg', '.jpeg', '.png'])) {
      this.setState({ errors: { filesize: 'File format not supported' } });
    }
    this.setState({
      ...this.state,
      imageChanged: true,
      filename: 'Attached file : ' + file_name,
      data: {
        ...this.state.data,
        masteritemimagefile: e.target.files[0]
      }
    });
  };
  hasExtension(fileName, exts) {
    return (new RegExp('(' + exts.join('|').replace(/\./g, '\\.') + ')$')).test(fileName);
  }

  handleInvalidSubmit = () =>{
    console.log(this.state.data)
  }

  handleValidSubmit = () => {

    const { data, imageChanged } = this.state;
    this.setState({ loading: true });
    const formdata = new FormData();
    if (imageChanged) {
      formdata.append('imagefile', data.masteritemimagefile);
    }
    // formdata.append("imagefile", data.masteritemimagefile);
    // formdata.append("filename", data.masteritemimagefile.name);
    formdata.append('id', data.id);
    formdata.append('masteritemtype', data.masteritemtype);
    formdata.append('masteritemname', data.masteritemname);
    formdata.append('masteritemdescription', data.masteritemdescription);
    formdata.append('masteritemshortdescription', data.masteritemshortdescription);
    
    formdata.append('masteritemuom', data.masteritemuom);
    formdata.append('masteritemcostperunit', data.costperunit);
    formdata.append('masteritemrate', data.masteritemrate);
    formdata.append('masteritemunitwt', data.masteritemunitwt);
    formdata.append('permeter', data.permeter);
    formdata.append('perfeet', data.perfeet);
    formdata.append('perinch', data.perinch);
    if (Object.keys(this.state.errors).length === 0) {
      this.props
        .submit(formdata)
        .then(() => {
          this.setState({ loading: false, errors: {}, success: true });
        })
        .catch(err => {
          this.setState({ errors: err.response.data.errors, loading: false });
        });
    } else if (this.state.errors.filesize) {
      alert(this.state.errors.filesize)
      this.setState({ loading: false, errors: {} });
    }
  };

  toggle = () => {
    this.setState({ ...this.state, success: false });
  };

  render() {
    const { data, errors, loading, success,filename } = this.state;
    const itemimg = this.state.data.itemImage != '' ?
    (<img src={data.itemImage} style={{'maxWidth': '150px','maxHeight' : '130px'}}/>) : ''
    return (
      <div>
        <div>
          {!!success ? (
            // <Alert color="info">Item is Updated successfully.</Alert>
            <Modal
              isOpen={success}
              toggle={this.toggle}
              className={this.props.className}
            >
              <ModalHeader  toggle={this.toggle}>Item</ModalHeader>
              <ModalBody >Item is Updated successfully.</ModalBody>
              <ModalFooter>
                <Button color='primary' onClick={this.toggle}>
                  Ok
                </Button>
              </ModalFooter>
            </Modal>
          ) : null}
        </div>
        <AvForm
          encType='multipart/form-data'
          onValidSubmit={this.handleValidSubmit}
          onInvalidSubmit={this.handleInvalidSubmit}
        >
          <Card>
            <CardHeader className='small-font-size-bold'>Add Item</CardHeader>
            <CardBody>
              <Row>
                <Col sm={2}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemtype'>Item Type</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      type='select'
                      name='masteritemtype'
                      onChange={this.onChange}
                      required={true}
                      value={data.masteritemtype || null}
                    >
                      <option value=''>select</option>
                      {/* <option value="5d10915e7ba4981da4e679f6">rawmaterial</option>
                                                <option value="5d10908a7ba4981da4e679f5">subassembly</option> */}
                      {this.createSelectItems()}
                    </AvField>
                  </AvGroup>
                </Col>
                <Col sm={1}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemname'>Item Code</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      name='masteritemname'
                      type='text'
                      id='masteritemname'
                      value={data.masteritemname || null}
                      readOnly
                      onChange={this.onChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: 'Please enter Item Name'
                        }
                      }}
                    />
                  </AvGroup>
                </Col>
                <Col sm={2}>
                  <AvGroup>
                    <Label className='small-font-size' for='itemName'>Item Name</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      name='itemName'
                      type='text'
                      id='itemName'
                      value={data.itemName}
                      onChange={this.onNameChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: 'Please enter Item Name'
                        }
                      }}
                    />
                  </AvGroup>
                </Col>                
              </Row>
              <Row>
                <Col sm={8}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemdescription'>Item Description</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      name='masteritemdescription'
                      type='textarea'
                      id='masteritemdescription'
                      value={data.masteritemdescription || null}
                      onChange={this.onChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: 'Please enter Item Description'
                        }
                      }}
                    />
                  </AvGroup>
                </Col>
                <Col sm={4}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemshortdescription'>Short Description</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      name='masteritemshortdescription'
                      type='textarea'
                      id='masteritemshortdescription'
                      value={data.masteritemshortdescription}
                      onChange={this.onNameChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: 'Please enter Item Short Description'
                        }
                      }}
                    />
                  </AvGroup>
                </Col>
              </Row>
              <Row>

                {/* <Col sm={6}>
                  <AvGroup>
                    <Label className='small-font-size' for="masteritemrate">Item Rate</Label>
                    <AvField
                      name="masteritemrate"
                      type="text"
                      id="masteritemrate"
                      value={data.masteritemrate}
                      onChange={this.onChange}
                      validate={{
                        minLength: {
                          value: 1,
                          errorMessage:
                            "Item Rate must be between 1 and 16 characters"
                        },
                        maxLength: {
                          value: 150,
                          errorMessage:
                            "Item Rate must be between 1 and 16 characters"
                        },
                        pattern: {
                          value: "^[0-9]*$",
                          errorMessage: "Please enter valid Item Rate"
                        }
                      }}
                    />
                  </AvGroup>
                </Col> */}
                <Col sm={2}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemuom'>UOM</Label>
                    <span className='required-label'>*</span>
                    <AvField
                      Label
                      type='select'
                      name='masteritemuom'
                      onChange={this.onChange}
                      required={true}
                      value={data.masteritemuom || null}
                    >
                      <option value=''>Please select value</option>
                      {/* <option value="kg">Kg</option>
                      <option value="liter">Liter</option>
                      <option value="kg">EA</option> */}
                      {this.createSelectUomItems()}
                    </AvField>
                  </AvGroup>
                </Col>
                <Col sm={1}>
                  <AvGroup>
                    <Label className='small-font-size' for='costperunit'>Unit cost</Label>
                    <AvField
                      Label
                      name='costperunit'
                      type='number'
                      id='costperunit'
                      value={data.costperunit || null}
                      onChange={this.onChange}
                    />
                  </AvGroup>
                </Col>
                <Col sm={2}>
                  <AvGroup>
                    <Label className='small-font-size' for='masteritemunitwt'>Unit weight in KG(per meter)</Label>
                    <AvField
                      className='small-font-size'
                      name='masteritemunitwt'
                      type='text'
                      id='masteritemunitwt'
                      value={data.masteritemunitwt}
                      onChange={this.onChange}

                    />
                  </AvGroup>
                </Col>
              </Row>
              <Row>
                <Col sm={1}>
                  <AvGroup>
                    <Label className='small-font-size' for='permeter'>Weight per meter</Label>
                    <AvField
                      Label
                      name='permeter'
                      type='number'
                      id='permeter'
                      value={data.permeter || null}
                      onChange={this.onChange2}
                    />
                  </AvGroup>
                </Col>
                <Col sm={1}>
                  <AvGroup>
                    <Label className='small-font-size' for='perfeet'>Weight per feet</Label>
                    <AvField
                      readOnly
                      className='small-font-size'
                      name='perfeet'
                      type='number'
                      id='perfeet'
                      
                      value={data.perfeet || null}
                      onChange={this.onChange}
                    />
                  </AvGroup>
                </Col>
                <Col sm={1}>
                  <AvGroup>
                    <Label className='small-font-size' for='perinch'>Weight per inch</Label>
                    <AvField
                      readOnly
                      className='small-font-size'
                      name='perinch'
                      type='number'
                      id='perinch'
                      value={data.perinch || null}
                      onChange={this.onChange}
                    />
                  </AvGroup>
                </Col>
              </Row>

              <Row>
                <Col sm={3}>
                  <AvGroup>
                    {/* <Label className='small-font-size' for='masteritemimagefile'>Item Image</Label> */}
                    {/* <span className='required-label'>*</span> */}
                    <AvField
                      Label
                      name='masteritemimagefile'
                      type='file'
                      id='masteritemimagefile'
                      style={{'display':'none'}}
                      onChange={this.fileSelectedHandler}
                      accept='.png,.svg,.gif,.jpg,.jpeg'
                    />
                    <label for="masteritemimagefile" style={{'color':'#379ccb','cursor':'pointer'}} >Click here to upload image</label>
                    <div style={{'color':'#707070','fontSize':'12px'}}>{filename}</div>
                  </AvGroup>
                </Col>

                 <Col sm={6}>
                {itemimg}
                </Col>
              </Row>
            </CardBody>
          </Card>
          <div className='mg-20'>
            <Button color='primary' disabled={loading} className='lgn-loader'>
              Submit
              {/* {loading} */}
            </Button>
          </div>
        </AvForm>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    materialoptionlistvalues: state.masteritemtypenamevalue,
    uomlistvalues: state.uomlistvalue,
    masteritemlistvalues: state.masteritemlistvalues
  };
}

export default connect(
  mapStateToProps,
  {}
)(UpdateItemListForm);
