import React from 'react';
import { Link } from 'react-router-dom';
import { Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { connect } from 'react-redux';
import { getItemsMoveOrder } from '../../../../actions/moveorder';
import { Button, Alert } from 'reactstrap';
import DataTable from '../../../../Components/Table/DatatablePage';
import CustomLoader from '../../../../Components/loader/loader';

class ViewMoveOrderProduction extends React.Component {
  state = {
    count: 0,
    loading: true,
    itemorderlistvalues: []
  };
  async componentDidMount() {
    const moveorderid = this.props.match.params.id;
    let receivedData = await getItemsMoveOrder(moveorderid);
    console.log("itemorder list values", receivedData);
    let quotationlist = Object.values(receivedData);
    //quotationlist= quotationlist.sort((a, b) => b.moveorderno.toLowerCase().localeCompare(a.moveorderno.toLowerCase()));
    quotationlist.sort(function(a, b){
      return parseInt(b.moveorderno) - parseInt(a.moveorderno);
    });
    const itemorderlistvalues = quotationlist.map(quotationvalue => {
      return {
        itemname: quotationvalue.itemname,
        itemdescription: quotationvalue.itemdescription,
        quantity: quotationvalue.quantity
      };
    });
    this.setState({ ...this.state, itemorderlistvalues });
    this.dataHandler(false);
  }
  async componentWillReceiveProps(props) {
    const moveorderid = this.props.match.params.id;
  }

  updateButton = id => {
    // let path = `/view_invoicepdf/${id}`;
    // window.open(path, '_blank');
    let path = `/view_invoicepdf/${id}`;
    //window.open(path, "_blank");
    this.props.history.push(path);
  };

  dataHandler = value => {
    this.setState({ loading: value });
  };

  render() {
    const { loading, count, itemorderlistvalues } = this.state;
    console.log(itemorderlistvalues);
    const columnvalue = [
      {
        label: 'Item Name',
        field: 'itemname',
        sort: 'asc',
        width: 40
      },
      {
        label: 'Item Description',
        field: 'itemdescription',
        sort: 'asc',
        width: 40
      },
      {
        label: 'quantity',
        field: 'quantity',
        sort: 'asc',
        width: 40
      }
    ];
    return (
      <div className='container mg-t-20'>
        {loading ? <CustomLoader /> : null}
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to='/admin_dashboard'>Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to='/transaction_page'>Transactions</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to='/move-order'>Move Order</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to='/moveorderlist'>Move Order List</Link>
          </BreadcrumbItem>          
          <BreadcrumbItem active>Items</BreadcrumbItem>
        </Breadcrumb>
        <div>
          <DataTable
            rowvalues={itemorderlistvalues}
            columnvalue={columnvalue}
            className="moveorderlist"
          />
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    moveorderlistvalues: state.moveorderlistvalues
  };
}

export default connect(
  mapStateToProps,
  { getItemsMoveOrder }
)(ViewMoveOrderProduction);
