import React from "react";
import { Link } from "react-router-dom";
import { Breadcrumb, BreadcrumbItem } from "reactstrap";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import ProductForm from "./ProductForm/ProductForm";
import { addproduct } from "../../actions/Product";
import { getMasterItemList } from "../../actions/Transaction";
import { getUomList } from "../../actions/uom";
import CustomLoader from "../../Components/loader/loader";

class Product extends React.Component {
  state = {
    loading: false
  };
  componentDidMount() {
    //this.props.getUomList();
    this.props.getMasterItemList();
  }
  submit = data => {
    this.dataHandler(true);
    return this.props
      .addproduct(data)
      .then(data => {
        this.dataHandler(false);
        return data;
      })
      .catch(err => {
        this.dataHandler(false);
        throw err;
      });
  };

  dataHandler = value => {
    this.setState({ loading: value });
  };
  render() {
    const { loading } = this.state;
    return (
      <div className="container mg-20">
        {loading ? <CustomLoader /> : null}
        <Breadcrumb>
          <BreadcrumbItem  Label>
            <Link to="/inventory_page">Setup</Link>
          </BreadcrumbItem>
          <BreadcrumbItem  Label active>Add Subassembly</BreadcrumbItem>
        </Breadcrumb>
        <ProductForm submit={this.submit} />
      </div>
    );
  }
}

ProductForm.propTypes = {
  addproduct: PropTypes.func.isRequired
};

export default connect(
  null,
  { addproduct, getMasterItemList,getUomList }
)(Product);
