import axios from "axios";

export default {
  invoice: {
    addinvoice: data => {
      return axios
        .post(`${process.env.REACT_APP_BASE_URL}/invoice/addinvoice`, {
          data
        })
        .then(res => {
          return res.data.invoicedata;
        });
    },
    updateinvoice: data => {
      return axios
        .post(`${process.env.REACT_APP_BASE_URL}/invoice/updateinvoice`, {
          data
        })
        .then(res => {
          return res.data.invoicedata;
        });
    },
    
    getinvoice: () => {
      return axios
        .get(`${process.env.REACT_APP_BASE_URL}/invoice/getinvoice`)
        .then(res => {
          return res.data.invoicelist;
        });
    },
    getproformainvoice: () => {
      return axios
        .get(`${process.env.REACT_APP_BASE_URL}/proformainvoice/getproformainvoice`)
        .then(res => {
          return res.data.invoicelist;
        });
    },
    searchquotation: customerid => {
      return axios
        .get(
          `${process.env.REACT_APP_BASE_URL}/quotation/searchquotation?q=${customerid}`
        )
        .then(res => {
          return res.data.customerquotationlist;
        });
    }
  }
};
