import React from "react";
import { Link } from "react-router-dom";
import { Breadcrumb, BreadcrumbItem } from "reactstrap";
import {
  getMasterItemList,
  getMasterItemTypes
} from "../../../../actions/Transaction";
import { connect } from "react-redux";
import { getUomList } from "../../../../actions/uom";
import { getCustomerList } from "../../../../actions/customerContact";
import { addinvoice } from "../../../../actions/Invoice";
import { getinvoice } from "../../../../actions/Invoice";
import { updateinvoice } from "../../../../actions/Invoice";

import { getChallan } from '../../../../actions/challan';
import ProformaInvoiceForm from "./ProformaInvoiceForm";
import CustomLoader from "../../../../Components/loader/loader";

class AddProformaInvoice extends React.Component {
  state = {
    loading: false,
    ID:this.props.match.params.id
  };
  componentDidMount() {
    this.props.getCustomerList();
    this.props.getMasterItemTypes();
    this.props.getMasterItemList();
    this.props.getUomList();
    this.props.getinvoice();
    this.props.getChallan();
  }
  valid = () =>{
    if( !localStorage.getItem('firstLoad') )
    {
      localStorage['firstLoad'] = true;
      window.location.reload();
    }  
    else
      localStorage.removeItem('firstLoad');
  }

  dataHandler = value => {
    this.setState({ loading: value });
  };
  render() {
    const { loading,ID } = this.state;
    return (
      <div className="container mg-t-20">
        {loading ? <CustomLoader /> : null}
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/admin_dashboard">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/transaction_page">Transactions</Link>
          </BreadcrumbItem>
          <BreadcrumbItem>
            <Link to="/invoice">Proforma Invoice</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Add Proforma Invoice</BreadcrumbItem>
        </Breadcrumb>
        <div>
          <ProformaInvoiceForm ID={ID} valid={this.valid}/>
        </div>
      </div>
    );
  }
}

export default connect(
  null,
  {
    getMasterItemList,
    getMasterItemTypes,
    getUomList,
    addinvoice,
    updateinvoice,
    getCustomerList,
    getinvoice,
    getChallan
  }
)(AddProformaInvoice);
