import React from "react";
import { connect } from "react-redux";
import { AvForm, AvField, AvGroup } from "availity-reactstrap-validation";
import { Button, Label, Alert } from "reactstrap";
import { Card, CardBody, CardHeader, Row, Col } from "reactstrap";
import PropTypes from "prop-types";

class AddMaterialForm extends React.Component {
  state = {
    data: {
      materialtype: "",
      materialname: "",
      materialdescription: "",
      materialuom: "",
      costperunit: null,
      materialrate: null
    },
    materialoptionlistvalues: [],
    errors: {},
    loading: false,
    success: false
  };

  onChange = e => {
    this.setState({
      ...this.state,
      data: { ...this.state.data, [e.target.name]: e.target.value }
    });
  };

  componentDidMount() {
    const materialoptionlistvalues = Object.values(
      this.props.materialoptionlistvalues
    );
    this.setState({ ...this.state, materialoptionlistvalues });
  }

  componentWillReceiveProps(props) {
    const materialoptionlistvalues = Object.values(
      props.materialoptionlistvalues
    );
    console.log(materialoptionlistvalues);
    this.setState({ ...this.state, materialoptionlistvalues });
  }

  createSelectItems = () => {
    let items = [];
    const materialoptionlistvalues = this.state.materialoptionlistvalues;
    materialoptionlistvalues.map((option, i) => {
      console.log(option);
      items.push(
        <option key={i} value={option.id}>
          {option.materialname}
        </option>
      );
    });
    return items;
  };

  handleValidSubmit = () => {
    console.log(this.state.data);
    this.setState({ loading: true });
    this.props
      .submit(this.state.data)
      .then(() => {
        this.setState({ loading: false, errors: {}, success: true });
        this.myFormRef.reset();
      })
      .catch(err => {
        this.setState({ errors: err.response.data.errors, loading: false });
      });
  };

  handleInvalidSubmit = () => {
    console.log("invalid submit");
  };

  render() {
    const { data, errors, loading, success } = this.state;
    return (
      <div>
        <div>
          {!!success ? (
            <Alert color="info">Material is added successfully.</Alert>
          ) : null}
        </div>
        <AvForm
          onValidSubmit={this.handleValidSubmit}
          onInvalidSubmit={this.handleInvalidSubmit}
          ref={el => (this.myFormRef = el)}
        >
          <Card>
            <CardHeader>Add Item</CardHeader>
            <CardBody>
              <Row>
                <Col sm={6}>
                  <AvGroup>
                    <Label className='small-font-size' for="materialname">Item Name</Label>
                    <span className="required-label">*</span>
                    <AvField
                      name="materialname"
                      type="text"
                      id="materialname"
                      value={data.materialname}
                      onChange={this.onChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: "Please enter Itemname"
                        },
                    
                        pattern: {
                          value: "^[a-zA-Z]",
                          errorMessage: "Please enter valid Itemname"
                        }
                      }}
                    />
                  </AvGroup>
                  <AvGroup>
                    <Label className='small-font-size' for="materialtype">Item Type</Label>
                    <span className="required-label">*</span>
                    <AvField
                      type="select"
                      name="materialtype"
                      onChange={this.onChange}
                    >
                      <option value="">select</option>
                      {/* <option value="5d10915e7ba4981da4e679f6">rawmaterial</option>
                                                <option value="5d10908a7ba4981da4e679f5">subassembly</option> */}
                      {this.createSelectItems()}
                    </AvField>
                  </AvGroup>
                  <AvGroup>
                    <Label className='small-font-size' for="materialuom">Item unit of measure</Label>
                    <span className="required-label">*</span>
                    <AvField
                      type="select"
                      name="materialuom"
                      onChange={this.onChange}
                    >
                      <option value="">Please select value</option>
                      <option value="kg">Kg</option>
                      <option value="liter">Liter</option>
                      <option value="kg">EA</option>
                    </AvField>
                  </AvGroup>
                </Col>
                <Col sm={6}>
                  <AvGroup>
                    <Label className='small-font-size' for="materialdescription">Item Description</Label>
                    <span className="required-label">*</span>
                    <AvField
                      name="materialdescription"
                      type="text"
                      id="materialdescription"
                      value={data.materialdescription}
                      onChange={this.onChange}
                      validate={{
                        required: {
                          value: true,
                          errorMessage: "Please enter itemdescription"
                        },
                  
                        pattern: {
                          value: "^[a-zA-Z]",
                          errorMessage: "Please enter valid itemdescription"
                        }
                      }}
                    />
                  </AvGroup>
                  <AvGroup>
                    <Label className='small-font-size' for="materialrate">Item Rate</Label>
                    <AvField
                      name="materialrate"
                      type="text"
                      id="materialrate"
                      value={data.materialrate}
                      onChange={this.onChange}
                      validate={{
                       
                        pattern: {
                          value: "^[0-9]*$",
                          errorMessage: "Please enter valid Itemrate"
                        }
                      }}
                    />
                  </AvGroup>
                  <AvGroup>
                    <Label className='small-font-size' for="costperunit">cost per unit</Label>
                    <AvField
                      name="costperunit"
                      type="text"
                      id="costperunit"
                      value={data.costperunit}
                      onChange={this.onChange}
                      validate={{
                   
                        pattern: {
                          value: "^[0-9]*$",
                          errorMessage: "Please enter valid costperunit"
                        }
                      }}
                    />
                  </AvGroup>
                </Col>
              </Row>
            </CardBody>
          </Card>
          <div className="mg-20">
            <Button color="primary" disabled={loading} className="lgn-loader">
              Submit
              {/* {loading} */}
            </Button>
          </div>
        </AvForm>
      </div>
    );
  }
}

AddMaterialForm.propTypes = {
  submit: PropTypes.func.isRequired
};

function mapStateToProps(state) {
  return {
    materialoptionlistvalues: state.materialoption
  };
}

export default connect(
  mapStateToProps,
  null
)(AddMaterialForm);
